import * as React from "react";
import { withRouter } from "react-router";
import { getHotSearch } from "../../api/index";
import { setHotSearchHits } from "../../api/bind"
import './searchtop.less';
import del from '../../assets/img/del.png'
const HISTORY_KEYWORDS = 'historyKeyWords';
declare const window;

interface ISprops {
    [propName: string]: any,
}
interface KeyWordItem {
    id: number,
    keywords: string
}
interface IState {
    focus: boolean,
    searchType: boolean,
    searchValue: string,
    hotKeyWords: any[],
    historyKeyWords: string[]
}
const style1 = {
    height: 'auto',
}
const style2 = {
    height: document.body.clientHeight,
}
const formStyles:any = {
    position:'fixed',
    left: '0',
    top: '0',
}
const inputFocus:any = {
    width: '3.1rem'
}

class AllForm extends React.Component<ISprops, IState>{
    constructor(props: any) {
        super(props);
        this.state = {
            focus: false,
            searchType: false,
            searchValue: '',
            hotKeyWords: [],
            historyKeyWords: []
        }
    }
    componentWillMount() {
        this.getHotSearchData();
        this.getHistoryKeyWords()
    }
    componentDidMount(){
        if( this.props.searchValues ) {
            this.setState({
                searchValue: this.props.searchValues
            })
        }

    }
    getHotSearchData() {
        getHotSearch().then(res => {
            if (!res.status) {
                this.setState({
                    hotKeyWords: res.data
                })
            }
        })
    }
    getHistoryKeyWords() {
        let localHis = localStorage.getItem(HISTORY_KEYWORDS);
        let historyKeyWords = localHis ? JSON.parse(localHis) : [];
        this.setState({
            historyKeyWords
        })
    }
    setHistoryKeyWords(keyWord) {
        let { historyKeyWords } = this.state;
        let checkIndex = historyKeyWords.indexOf(keyWord);
        // 检查现有历史是否存在
        if (checkIndex > -1) {
            historyKeyWords.splice(checkIndex, 1)
        }
        // 将最新插在最前
        historyKeyWords.unshift(keyWord);
        localStorage.setItem(HISTORY_KEYWORDS, JSON.stringify(historyKeyWords));
    }
    changeValue(e: any) {
        //将当前的合成事件从事件池中移除
        e.persist()
        this.setState({
            searchValue: e.target.value,
        }, () => {
            this.setState({ searchType: !!e.target.value })
        })
    }
    handleClickKeywords = (searchValue: string) => {
        this.setState({
            searchValue
        }, () => {
            this.setState({ searchType: true })
            this.handleSearch()
        })
    }
    handelHotClick = (searchValue: string, id: number) => {
        this.setState({
            searchValue
        }, () => {
            setHotSearchHits({
                id: id
            }).then(res => {
                console.log(res)
            })
            this.setState({ searchType: true })
            this.handleSearch()
        })
    }
    handleSearch = () => {
        let { searchValue } = this.state;
        this.setHistoryKeyWords(searchValue)
        let source = this.props.searchForm;
        if(source === 0) {
            this.props.history.push(`/courseList/${searchValue}`)
        } else if (source === 1) {
            this.props.history.push(`/vip/index/${searchValue}`)
        }
        
    }
    focusSearch = () => {
        this.setState({
            focus: true
        })
    }
    delFocus = () => {
        let { focus } = this.state;
        let source = this.props.searchForm;
        let {isIndex} = this.props
        this.setState({
            focus: false
        })
        if(source === 0) {
            if(!isIndex) {
                this.props.history.push(`/courseList`)
            }
        } else if (source === 1) {
            if(!isIndex) {
                this.props.history.push(`/vip/index`)
            }
        }
    }
    delHistory = () => {
        localStorage.removeItem(HISTORY_KEYWORDS)
        this.setState({historyKeyWords: []})
    }
    render() {
        const { searchType, searchValue, hotKeyWords, historyKeyWords, focus } = this.state;
        const { formStyle, searchValues } = this.props;
        // console.log(searchValues)
        return (
            <div className="all-form" style={focus ? style2 : (formStyle ? formStyles : style1 )}>
                <div className="form-item">
                    <input type="text" name="" className="search-tit"
                        value={searchValue}
                        placeholder={this.props.title}
                        onChange={(e) => this.changeValue(e)}
                        onFocus={this.focusSearch}
                        style={ focus ? {} : inputFocus}
                    />
                    {
                        focus ? 
                            searchType ?
                                <input type="submit" className="search-submit" value='提交' onClick={this.handleSearch} />
                                :
                                <input type="button" className="search-submit" value='取消' onClick={this.delFocus} />
                        :
                        ''
                    }
                </div>
                {
                    focus ?
                        <React.Fragment>
                            <div className="hot-form">
                                <h3>
                                    热门搜索
                                </h3>
                                <div className="hot-form-item clearfix">
                                    {
                                        hotKeyWords.map((el, index) => (
                                            <span
                                                key={el.id}
                                                onClick={() => this.handelHotClick(el.keywords,el.id)}
                                            >{el.keywords}</span>
                                        ))
                                    }
                                </div>
                            </div>

                            <div className="history-form">
                                <h3>搜索历史  <span onClick={this.delHistory} style={{float:'right',width:'.15rem'}}><img style={{width:'100%'}} src={del} alt=""/></span></h3>
                                <div className="history-form-item clearfix">
                                    {
                                        historyKeyWords.map((word, index) => (
                                            <span
                                                key={index}
                                                onClick={() => this.handleClickKeywords(word)}
                                            >{word}</span>
                                        ))
                                    }
                                </div>
                            </div>
                        </React.Fragment>
                        :
                        <div></div>
                }


            </div>

        )
    }
}

//@ts-ignore
export default withRouter(AllForm);