import * as React from "react";
import { ListView } from 'antd-mobile';
import CourseItem from './courseItem'

//redux 基础组件加载
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as Actions from '../actions';
import { getHotCourse } from '../api/index' 




const mapStateToProps = (state: any) => {
    return {
        store: state
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        ...bindActionCreators(Actions, dispatch)
    }
}

interface ISprops {
    [propName: string]: any,

}
interface IState {
    dataSource: any,
    isLoading: Boolean,
    hasMore: Boolean
}
class IndexList extends React.Component<ISprops, IState>{
    constructor(props: any) {
        super(props);
        const dataSource = new ListView.DataSource({
            rowHasChanged: (row1, row2) => row1 !== row2,
        });
      
        this.state = {
            dataSource,
            isLoading: true,
            hasMore: true
        }
    }
    courseList: Object[] = [];
    page_num: number = 1;
    page_size: number = 10;

    componentWillMount() {
        
    }
    componentDidMount() {
        this.queryListData()
    }
    queryListData() {
        this.setState({ isLoading: true });

        let {page_num, page_size} = this
        getHotCourse({
            page_num,
            page_size
        }).then(res => {
            if(!res.status) {
                let {list, total_pages, page_num} = res.data;
                // 新获取的数据直接拼接
                this.courseList = [...this.courseList, ...list];
                
                // 如果还有下一页，页码加1
                if(page_num < total_pages) {
                    this.page_num += 1
                }

                this.setState({
                    dataSource: this.state.dataSource.cloneWithRows(this.courseList),
                    isLoading: false,
                    hasMore: page_num < total_pages
                })
            }
        })

    }
    onEndReached = (event) => {
        // load new data
        // hasMore: from backend data, indicates whether it is the last page, here is false
        if (this.state.isLoading || !this.state.hasMore) {
            return;
        }
        this.queryListData()
    }
    render() {
        const renderFooter = () => (
            <div style={{ textAlign: 'center', lineHeight: '.26rem' }}>{this.state.isLoading ? 'Loading...' : 'Loaded'}</div>
        )
        const Row = (rowData, sectionID, rowID) => (
            <CourseItem key={rowID} course={rowData}/>
        )
        return (
            <ListView
                dataSource={this.state.dataSource}
                renderFooter={renderFooter}
                renderRow={Row}
                className="index-list"
                pageSize={4}
                useBodyScroll
                onScroll={() => { console.log('scroll'); }}
                scrollRenderAheadDistance={500}
                onEndReached={this.onEndReached}
                onEndReachedThreshold={10}
            />

        )
    }

}

export default connect(mapStateToProps, mapDispatchToProps)(IndexList);
