//react 基础组件加载
import * as React from "react";

//redux 基础组件加载
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as Actions from '../actions';

//页面样式加载 组件加载
import FootBar from '../components/footer'
import Banner from '../components/banner'
import NavBar from '../components/search/navBar'
import HotList from '../components/hotList'
import IndexAdvertising from '../components/advertising/indexAdvertising'

import AllForm from '../components/search/allForm'
import { setLocal, refreshStorage } from '../util/config'
import '../assets/less/index.less'


const mapStateToProps = (state: any) => {
    return {
        store: state
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        ...bindActionCreators(Actions, dispatch)
    }
}


interface ISprops {
    
}
interface IState {
    advertising:boolean,
    footType: number,
    formStyle: boolean
}

class Index extends React.Component<ISprops,IState> {
    constructor(props: any){
        super(props);
        this.state = {
            advertising:false,//是否显示广告位
            footType: 1,//footerbar
            formStyle: true
        }
    }
    checkAd() {
        //判断是否现实弹窗广告
        let show = localStorage.getItem('advertising');
        if(!show || !refreshStorage('advertising',24*60*60*1000)) {
            setLocal('advertising','true');
            this.setState({advertising: true})
        } else {
            this.setState({advertising: false})
        }
    }
    componentDidMount() {
        document.title = '高顿会员服务中心'
        this.checkAd()
    }
    render() {
        const { advertising, footType, formStyle } = this.state;
        return (
            <React.Fragment>
                <div className="search-top">
                    <AllForm title="请输入关键字" isIndex={true} formStyle={formStyle} searchForm={0}/>
                </div>
                <Banner/>
                <NavBar/>
                <HotList/>
                <FootBar footType={footType}/>
                {
                    advertising ?
                    <IndexAdvertising title="home-layer"/>
                    :
                    <div></div>
                }
                
            </React.Fragment>
        );
    }

}


export default connect(mapStateToProps, mapDispatchToProps)(Index);