import * as React from "react";
import { Link } from 'react-router-dom'
import { getCourseSeries } from '../../api/index'

import './searchtop.less';

// import nav1 from '../../assets/img/index/nav1.png'
// import nav2 from '../../assets/img/index/nav2.png'
// import nav3 from '../../assets/img/index/nav3.png'
// import nav4 from '../../assets/img/index/nav4.png'
// import nav5 from '../../assets/img/index/nav5.png'
// import nav6 from '../../assets/img/index/nav6.png'
// import nav7 from '../../assets/img/index/nav7.png'
import nav8 from '../../assets/img/index/nav8.png'


interface ISprop{
    
}
interface IState {
    navbarlist:any,
}

const NavBarList = (props: any) => {
    
    return (
        (props.navbarlist || []).map( (el: any,index: any)=>{
            let path = {
                pathname:'/courseList',
                query:el.id,
            }
            return(
                <div className="nav-bar-i" key={index}>
                    <Link to={path}>
                        <img src={el.icon} alt={el.name}/>
                        <p>{el.name}</p>
                    </Link>
                </div>
            )
        })
    )
}

export default class NavBar extends React.Component<ISprop,IState>{
    constructor(props: any) {
        super(props);
        this.state = {
            navbarlist: [
            ]
        }
    }
    componentDidMount() {
        getCourseSeries().then(res => {
            if (!res.status) {
                let arr = res.data.splice(0,7)
                this.setState({
                    navbarlist: arr
                })
            }
        })
    }
    render() {
        const {navbarlist} = this.state;
        return (
            <div className="nav-bar">
                <NavBarList navbarlist={navbarlist} />
                <div className="nav-bar-i">
                    <Link to="/allsearch">
                        <img src={nav8} alt=""/>
                        <p>全部</p>
                    </Link>
                </div>
            </div>

        )
    }

}
