import * as React from "react";
import {withRouter} from "react-router";
import {getCourseTag} from "../util"
import '../assets/less/courseItem.less'

interface ISprops {
    [propName: string]: any,
}
interface IState {
    dataSource: any,
    isLoading: Boolean,
    hasMore: Boolean
}
class CourseItem extends React.Component<ISprops, IState> {

    handleClickList = (id: string) => {
        //进入详情页
        this.props.history.push('/courseDetail/'+id)
    }
    render() {
        const { course } = this.props;
        course.code = getCourseTag(course);
        return (
            <div className="course-item clearfix" key={course.id} onClick={() => this.handleClickList(course.id)}>
                <div className="tit-tips">
                    <span className="tit-tips-l"></span>
                    <span className="tit-tips-r">{course.course_type}</span>
                </div>
                <div className="course-item-l left">
                    <h2>{course.course_name}</h2>
                    <div className="bot-tips clearfix">
                        {
                            course.code.map( (element,indexs)=>(
                                <span className="left" key={indexs}>{element}</span>
                            ))
                        }
                    </div>
                </div>
                <div className="course-item-r right">
                    <img src={course.img_url} alt={course.course_name} />
                </div>
            </div>
        )
    }

}

//@ts-ignore
export default withRouter(CourseItem);
