import * as React from "react";

import { getAd, getAdInfo } from '../../api/index';
import './advertising.less';

import close from '../../assets/img/index/close.png';

interface ISprops {
    [propName: string]: any,

}
interface IState {
    [statesName: string]: any
}

export default class IndexAdvertising extends React.Component<ISprops, IState>{
    constructor(props: any) {
        super(props);
        this.state = {
            showFlag: true,
            adDetail:[]
        }
    }
    getInitAd(title) {
        getAd({
            "label": title
        }).then(res => {
            if (!res.status) {
                this.setState({adDetail:res.data[0]})
            }
        })
    }
    handleBannerClick(el) {
        getAdInfo({
            id: el.id
        }).then(res => {
            window.location.href = el.link;
        })
    }
    componentWillMount() {
        const { title } = this.props;
        this.getInitAd(title)
    }
    render() {
        const { showFlag, adDetail } = this.state;
        return (
            <div className="index-advertising">
            {
                showFlag && adDetail && Object.keys(adDetail).length !== 0 ? 
                    <div className="advertising-mask">
                        <div className="advertising">
                            <img className="advertising-img" src={adDetail.banner} alt={adDetail.name} onClick={() => this.handleBannerClick(adDetail)}/>
                            <div className="advertising-close">
                                <img src={close} onClick={() => {this.setState({showFlag:false})}} alt="" />
                            </div>
                        </div>
                    </div>
                    :
                    <div></div>
            }    
                
            </div>

        )
    }

}
