import * as React from "react";
import { Carousel } from 'antd-mobile';
import { getAd, getAdInfo } from '../api/index';


import '../assets/less/banner.less';

interface BannerItem {
    id: string,
    banner: string,
    link: string,
    name: string,
}
export default class Banner extends React.Component{
    state = {
        data: []
    }
    componentWillMount() {
        this.getBanner()
    }
    getBanner() {
        getAd({
            "label": "home-banner"
        }).then(res => {
            if(!res.status) {
                this.setState({
                    data: res.data
                })
            }
        })
    }
    handleBannerClick(el) {
        getAdInfo({
            id: el.id
        }).then(res => {
            window.location.href = el.link;
        })
    }


    render() {
        let { data } = this.state
        return (
            <div className="banner">
                <Carousel
                    autoplay={true}
                    infinite
                    style={{width: "100%", height: "1.65rem"}}
                    frameOverflow="visible"
                    autoplayInterval={3000}
                    >
                    {data.map((el: BannerItem,index) => (
                        <div
                            key={index}
                            className="bannerLink"
                        >
                            <img
                                src={el.banner}
                                alt={el.name}
                                className="bannerImg"
                                onClick={() => this.handleBannerClick(el)}
                            />
                        </div>
                    ))}
                </Carousel>
            </div>
        )
    }

}
