import * as React from "react";
import { Link } from 'react-router-dom'
import '../assets/less/footer.less'


import index1 from '../assets/img/footer/index1.png';
import course1 from '../assets/img/footer/course1.png';
import vip1 from '../assets/img/footer/vip1.png';
import my1 from '../assets/img/footer/my1.png';


import index2 from '../assets/img/footer/index2.png';
import course2 from '../assets/img/footer/course2.png';
import vip2 from '../assets/img/footer/vip2.png';
import my2 from '../assets/img/footer/my2.png';


interface ISprops {
    footType: number,
    
}
interface IState {
}
export default class FootBar extends React.Component<ISprops,IState>{
    componentDidMount(){

    }
    render() {
        const { footType } = this.props;
        return(
            <div className="footer">
                <Link to='/' className="footer-item">
                    <img src={ footType === 1 ? index2 : index1} alt=""/>
                    <span className={ footType === 1 ? 'active' : ''}>首页</span>
                    
                </Link>
                <Link to="/courseList" className="footer-item">
                    <img src={ footType === 2 ? course2 : course1} alt=""/>
                    <span className={ footType === 2 ? 'active' : ''}>课程</span>
                </Link>
                <Link to="/vip/index" className="footer-item">
                    <img src={ footType === 3 ? vip2 : vip1} alt=""/>
                    <span className={ footType === 3 ? 'active' : ''}>vip</span>
                </Link>
                <Link to="/mine/index" className="footer-item">
                    <img src={ footType === 4 ? my2 : my1} alt=""/>
                    <span className={ footType === 4 ? 'active' : ''}>我的</span>
                </Link>
            </div>
        )
    }

}
